// #region
.wrapper {
	display: flex;
	flex-direction: column;
	min-height: 100%;
	margin: 0 auto;
}

[class*='_container'] {
	max-width: $maxWidthContainer;
	box-sizing: content-box;
	margin: 0 auto;
	padding: 0 10px;
}

[class*='icon-'] {
	cursor: pointer;
	height: 100%;
	@include flex-align-center-justify-center;
}
