.bread-crumbs {
	// .bread-crumbs__list
	&__list {
		li:not(:last-child) {
			margin-right: 0.5rem;
		}
		@include flex-align-center;
	}

	// .breadcrumbs__link
	&__link {
		color: $color;
		font-weight: 700;
		&:hover {
			color: $font-color-defoult;
		}
	}

	// .breadcrumbs__item

	&__item {
	}
}
